}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.header {
  background-image: url("../img/main-banner-bg.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  text-align: center;
}

.header__items {
  display: flex;
  justify-content: space-between;
  padding: 30px 0;
}

.header__logo a {
  font-size: 30px;
  color: #eaffd0;
}

.header__btn {
  padding: 10px 30px;
  box-sizing: border-box;
  background: transparent;
  border: 2px solid #eaffd0;
  text-transform: uppercase;
  font-size: 14px;
  color: #eaffd0;
  transition: background .3s;
}

.header__btn:hover {
  background: rgba(255, 255, 255, 0.2);
}

.menu__list {
  display: flex;
  font-size: 14px;
  text-transform: uppercase;
}

.menu__item a {
  position: relative;
  padding: 10px 15px;
  display: block;
  color: #eaffd0;
  transition: all .3s;
}

.menu__item a:hover {
  color: #fce38a;
}

.menu__item a:hover:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -32%;
  width: 64%;
  height: 2px;
  background: #fce38a;
}

h1 {
  position: relative;
  margin: 20px 0 50px;
  padding-bottom: 40px;
  text-transform: uppercase;
  font-size: 120px;
  line-height: 0.95;
  color: #eaffd0;
}

h1:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -30px;
  width: 60px;
  height: 2px;
  background: #eaffd0;
}

h2 {
  margin: 70px 0 20px;
  font-size: 72px;
  color: #eaffd0;
}

h3 {
  margin: 10px 0 40px;
  text-align: center;
  text-transform: uppercase;
  font-size: 30px;
  color: #333333;
}

h3:after {
  content: "";
  margin: 40px auto 40px;
  display: block;
  width: 60px;
  height: 2px;
  background: #f38181;
}

.h3sub {
  margin: 0 0 10px;
  padding-top: 80px;
  text-align: center;
  font-size: 24px;
  color: #333333;
}

.h3about {
  margin: 40px 0 100px;
  text-align: center;
  font-size: 15px;
  color: #999999;
}

h4 {
  font-size: 14px;
  text-transform: uppercase;
  color: #333333;
}

h5 {
  text-transform: uppercase;
  font-size: 14px;
  color: #333333;
}

.banner-slider {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  margin-top: 150px;
}

.banner-slider__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% / 12 * 3 - 30px);
  text-align: left;
  text-transform: uppercase;
  font-size: 18px;
  color: #eaffd0;
}

.banner-slider__item span {
  font-size: 24px;
}

.banner-slider__item {
  padding: 25px 0;
  border-top: 2px solid #eaffd0;
}

.about {
  margin-bottom: 100px;
}

.about .team {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.about .team__item {
  word-wrap: break-word;
  padding-left: 15px;
  padding-right: 15px;
  width: calc(100% / 12 * 4 - 30px);
}

.counter {
  background: #95e1d3;
}

.counter__wrapper {
  display: flex;
}

.counter__item {
  padding: 80px 0;
  width: 20vw;
  text-align: center;
  color: #fff;
  border-right: 1px solid #fff;
}

.counter__item:first-child {
  border-left: 1px solid #fff;
}

.counter__item .amount {
  display: block;
  font-size: 72px;
}

.counter__item .description {
  text-transform: uppercase;
  font-size: 14px;
}

.service__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  margin: 40px 0 120px;
}

.service__wrapper .block {
  word-wrap: break-word;
  padding-left: 15px;
  padding-right: 15px;
  width: calc(100% / 12 * 4 - 30px);
  margin-top: 40px;
  padding-bottom: 40px;
  display: flex;
}

.service__wrapper .block:nth-child(-n+3) {
  border-bottom: 1px solid #e5e5e5;
}

.service__wrapper .block__img {
  width: 30%;
  text-align: center;
}

.service__wrapper .block__img img {
  width: 40px;
}

.service__wrapper .block__info {
  width: 70%;
}

.service__wrapper .block__paragraph {
  padding-top: 10px;
  font-size: 14px;
  line-height: 22px;
  color: #999999;
}

.design {
  margin-bottom: 80px;
  background-image: url("../img/design-banner-bg.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.design__imgs {
  position: relative;
  text-align: center;
}

.design__img-tablet {
  margin-bottom: -4px;
}

.design__img-phone {
  position: absolute;
  bottom: 0;
  margin-bottom: -90px;
  left: 50%;
  margin-left: 100px;
}

.offer__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.offer__img {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% / 12 * 6 - 30px);
}

.offer__accordion {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% / 12 * 6 - 30px);
}

.accordion {
  margin-bottom: 5px;
}

.accordion__header {
  padding: 15px 0;
  display: flex;
  align-items: center;
  border: 1px solid #cccccc;
}

.accordion__header img {
  padding: 0 15px;
}

.accordion__header i {
  margin-right: 0;
  margin-left: auto;
  padding: 0 15px;
  color: #cccccc;
  font-size: 24px;
}

.accordion__info {
  display: none;
  padding: 20px 25px 25px 20px;
  border: 1px solid #cccccc;
  border-top: 0;
  font-size: 15px;
  line-height: 24px;
  color: #999999;
}

.accordion__info_active {
  display: block;
}

.accordion__info p {
  padding-right: 20px;
  height: 135px;
  overflow: scroll;
}

.reviews {
  margin-top: 120px;
  background: #f8f8f8;
}

.reviews-slider__wrapper {
  position: relative;
  padding: 70px 0;
}

.reviews-slider__item {
  margin: 0 auto;
  width: 90%;
  display: flex;
}

.reviews-slider__icon {
  min-width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #95e1d3;
  color: #95e1d3;
  font-size: 72px;
}

.reviews-slider__photo {
  position: relative;
  min-width: 215px;
  height: 215px;
  border-radius: 50%;
  overflow: hidden;
}

.reviews-slider__photo img {
  height: 100%;
  width: auto;
  max-width: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.reviews-slider__review {
  margin: 0 55px;
}

.reviews-slider__review_photo {
  align-self: center;
}

.reviews-slider__review p {
  padding-bottom: 30px;
  font-size: 24px;
  color: #999999;
}

.reviews-slider__review span {
  font-size: 24px;
  color: #333333;
}

.reviews-slider__review span:before {
  content: "";
  margin-right: 10px;
  display: inline-block;
  width: 60px;
  height: 3px;
  background: #f38181;
}

.reviews-slider__btn {
  margin-top: -20px;
  position: absolute;
  top: 50%;
  color: #cccccc;
  font-size: 20px;
}

.reviews-slider__btn_next {
  right: 0;
}

.our-team {
  margin-bottom: 100px;
}

.our-team__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.our-team .person {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% / 12 * 4 - 30px);
  text-align: center;
}

.our-team .person__photo {
  background: #95e1d3;
  cursor: pointer;
}

.our-team .person__photo img {
  vertical-align: middle;
  transition: all .5s;
}

.our-team .person__photo img:hover {
  transform: translate(-10px, -10px);
}

.our-team .person__about {
  margin-top: 30px;
  font-size: 14px;
}

.our-team .person__about .name {
  text-transform: uppercase;
  color: #333333;
}

.our-team .person__about .position {
  line-height: 38px;
  color: #999999;
}

.logos {
  margin-top: 100px;
  padding: 70px 0;
  background: #f8f8f8;
}

.logos__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.logos__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% / 12 * 2 - 30px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.work {
  position: relative;
}

.work__offset {
  padding-top: 50%;
  height: 0;
}

.work__content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.work__content .item {
  flex: 1 1 50%;
  width: 25%;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.work__content .item_1 {
  background-image: url("../img/pic1.jpg");
}

.work__content .item_2 {
  background-image: url("../img/pic2.jpg");
}

.work__content .item_3 {
  background-image: url("../img/pic3.jpg");
}

.work__content .item_4 {
  background-image: url("../img/pic4.jpg");
}

.work__content .item_5 {
  flex: 1 1 100%;
  background-image: url("../img/pic5.jpg");
}

.work__content .item_6 {
  background-image: url("../img/pic6.jpg");
}

.work__content .item_7 {
  background-image: url("../img/pic7.jpg");
}

.clients {
  background-image: url("../img/bg-clients.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.clients__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  margin-top: 80px;
  padding-bottom: 100px;
}

.clients__wrapper .client {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% / 12 * 6 - 30px);
  margin-bottom: 30px;
  display: flex;
}

.clients__wrapper .client__photo {
  flex: 1 0 30%;
  text-align: center;
}

.clients__wrapper .client__opinion {
  flex: 1 1 55%;
  font-size: 14px;
}

.clients__wrapper .client__opinion .name {
  display: block;
  text-transform: uppercase;
  color: #333333;
}

.clients__wrapper .client__opinion .pos {
  line-height: 38px;
  color: #333333;
}

.clients__wrapper .client__opinion .pos:after {
  content: "";
  margin: 10px 0 15px;
  display: block;
  width: 60px;
  height: 2px;
  background: #f38181;
}

.clients__wrapper .client__opinion .review {
  color: #999999;
}

.blog {
  margin: 40px 0 90px;
}

.blog__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.story {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% / 12 * 4 - 30px);
  display: flex;
  flex-direction: column;
}

.story .story-banner {
  position: relative;
}

.story .story-banner__date {
  padding: 15px;
  position: absolute;
  bottom: 6%;
  left: -3%;
  background: #95e1d3;
  font-size: 30px;
  color: #fff;
}

.story .story-banner__date span {
  display: block;
  font-size: 15px;
}

.story h5 {
  padding: 20px 0;
}

.story__info {
  margin-bottom: 20px;
  font-size: 15px;
  line-height: 25px;
  color: #999999;
}

.story__footer {
  margin: auto 0 0;
  padding: 20px 0;
  border-top: 1px solid #ccc;
  color: #999999;
}

.story__footer i:before {
  color: #95e1d3;
}

.story__footer i:first-child {
  padding-right: 10px;
}

.map {
  padding: 60px 0 50px;
  background: url("../img/bg-map.jpg");
  position: center center;
  background-size: cover;
  text-align: center;
  color: #f38181;
}

.map__icon {
  font-size: 24px;
}

.map__link {
  margin: 20px 0;
  display: block;
  text-transform: uppercase;
}

.map__link:after {
  content: "";
  margin: 20px auto;
  display: block;
  width: 60px;
  height: 2px;
  background: #f38181;
}

.footer {
