  background: #f8f8f8;
}

.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  padding-bottom: 70px;
  border-bottom: 1px solid #cccccc;
}

.footer__item_1 {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% / 12 * 5 - 30px);
}

.footer__item_2 {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% / 12 * 4 - 30px);
}

.footer__item_3 {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% / 12 * 3 - 30px);
}

.footer__subhead {
  margin-bottom: 40px;
  display: block;
  text-transform: uppercase;
  font-size: 14px;
  color: #333333;
}

.footer__logo {
  margin: 40px  0;
  color: #999999;
  font-size: 45px;
}

.footer__info {
  margin: 40px 0 50px;
  font-size: 15px;
  color: #999999;
}

.footer__follow {
  margin: 50px 0 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #ccc;
  font-size: 14px;
  color: #333333;
}

.footer__follow span {
  font-size: 18px;
}

.social {
  margin: 20px 0 40px;
  display: flex;
}

.social__item {
  margin: 0 5px;
}

.social__item i {
  color: #95e1d3;
}

.social__head {
  font-size: 15px;
  color: #999999;
}

.article {
  margin: 30px 0;
  display: flex;
  justify-content: space-between;
}

.article__img {
  vertical-align: middle;
}

.article__cont {
  align-self: center;
  flex: 1 1 70%;
  padding: 0 5%;
  align-self: center;
}

.article__cont p {
  text-transform: uppercase;
  font-size: 12px;
}

.article__cont span {
  font-size: 13px;
  color: #999999;
}

.insta-galery {
  margin: 40px 0 20px;
  display: flex;
  flex-wrap: wrap;
}

.insta-galery__item {
  margin: 0 2px 2px 0;
  flex: 1 1 30%;
  align-self: center;
}

.insta-galery__item img {
  width: 100%;
  vertical-align: middle;
}

.show-link {
  font-size: 13px;
  color: #999999;
}

.form__input {
  width: 40%;
  padding: 12px;
  border: 1px solid #cccccc;
  font-size: 15px;
  color: #333333;
}

.form__input::-webkit-input-placeholder {
  color: #ccc;
}

.form__input::-moz-placeholder {
  color: #ccc;
}

.form__input:-moz-placeholder {
  color: #ccc;
}

.form__input:-ms-input-placeholder {
  color: #ccc;
}

.form__btn {
  margin-left: -5px;
  padding: 12px 7%;
  text-transform: uppercase;
  font-size: 14px;
  border: 1px solid #95e1d3;
  background: #95e1d3;
  color: #fff;
  cursor: pointer;
}

.copyright {
  padding: 25px 0;
  text-align: center;
  font-size: 14px;
  color: #333333;
}

.copyright span {
  color: #f38181;
}

@media screen and (max-width: 1200px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 576px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 420px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}