//The main colors of the page
$color_pink: #f38181;
$color_yellow: #fce38a;
$color_white: #eaffd0;
$color_turquoise: #95e1d3;
$color_black: #333333;
$color_gray: #999999;

@include reset();

a{
    text-decoration: none;
}

//reset defaults styles for scroll
::-webkit-scrollbar-button {
background-image:url('');
background-repeat:no-repeat;
width:5px;
height:0px;
}

::-webkit-scrollbar-track {
background-color: transparent;
}

::-webkit-scrollbar-thumb {
-webkit-border-radius: 5px;
border-radius: 5px;
background-color:$color_turquoise;
}

::-webkit-scrollbar-thumb:hover{
background-color:#60d2bd;
}

::-webkit-resizer{
background-image:url('');
background-repeat:no-repeat;
width:4px;
height:0px;
}

::-webkit-scrollbar{
width: 7px;
}

.container{
    @include wrapper();
}

// .sm-debug{
//     @include debug(rgba(0, 0, 0, 0.5), 1px solid #ff0);
// }

.header{
	background-image: url('../img/main-banner-bg.jpg');
	background-position: center center;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
	text-align: center;

	&__items{
		display: flex;
		justify-content: space-between;
		padding: 30px 0;
	}

	&__logo{
		a{
			@extend %bold-m;
			font-size: 30px;
			color: $color_white;
		}
	}
	&__btn{
		@extend %bold-m;
	    padding: 10px 30px;
		box-sizing: border-box;
		background: transparent;
		border: 2px solid $color_white;
		text-transform: uppercase;
		font-size: 14px;
		color: $color_white;
		transition: background .3s;
		&:hover{
			background: rgba(255, 255, 255, .2);
		}
	}
}

.menu{
	&__list{
		@extend %regular-r;
		display: flex;
		font-size: 14px;
		text-transform: uppercase;
	}
	&__item{

		a{
			position: relative;
			padding: 10px 15px;
			display: block;
			color: $color_white;
			transition: all .3s;
			&:hover{
				color: $color_yellow;
				&:after{
					content: "";
					position: absolute;
					bottom: 0;
					left: 50%;
					margin-left: -32%;
					width: 64%;
					height: 2px;
					background: $color_yellow;
				}
			}
		}
	}
}

h1{
	@extend %bold-m;
	position: relative;
	margin: 20px 0 50px;
	padding-bottom: 40px;
	text-transform: uppercase;
	font-size: 120px;
	line-height: 0.95;
	color: $color_white;
	&:after{
		content: "";
		position: absolute;
		bottom: 0;
		left: 50%;
		margin-left: -30px;
		width: 60px;
		height: 2px;
		background: $color_white;
	}
}

h2{
	@extend %regular-ks;
	margin: 70px 0 20px;
	font-size: 72px;
	color: $color_white;
}

h3{
	@extend %bold-m;
	margin: 10px 0 40px;
	text-align: center;
	text-transform: uppercase;
	font-size: 30px;
	color: $color_black;
	&:after{
		content: "";
		margin: 40px auto 40px;
		display: block;
		width: 60px;
		height: 2px;
		background: $color_pink;
	}
}

.h3sub{
	@extend %regular-ks;
	margin: 0 0 10px;
	padding-top: 80px;
	text-align: center;
	font-size: 24px;
	color: $color_black;
}

.h3about{
	@extend %regular-r;
	margin: 40px 0 100px;
	text-align: center;
	font-size: 15px;
	color: $color_gray;
}

h4{
	@extend %regular-m;
	font-size: 14px;
	text-transform: uppercase;
	color: $color_black;
}

h5{
	@extend %bold-m;
	text-transform: uppercase;
	font-size: 14px;
	color: $color_black;
}

.banner-slider{
	@include row-flex();
	margin-top: 150px;
	&__item{
		@include col();
		@include size(3);
		@extend %regular-m;

		text-align: left;
		text-transform: uppercase;
		font-size: 18px;
		color: $color_white;

		span{
			@extend %bold-m;
			font-size: 24px;
		}
	}
}
.banner-slider{
	&__item{
		padding: 25px 0;
		 border-top: 2px solid $color_white;
	}
}

.about{
	margin-bottom: 100px;
	.team{
		@include row-flex();

		&__item{
			@include col-padding();
			@include size(4);
		}
	}
}

.counter{
	background: $color_turquoise;
	&__wrapper{
		display: flex;
	}
	&__item{
		padding: 80px 0;
		width: 20vw;
		text-align: center;
		color: #fff;
		border-right: 1px solid #fff;
		&:first-child{
			border-left: 1px solid #fff;
		}
		.amount{
			@extend %bold-m;
			display: block;
			font-size: 72px;
		}
		.description{
			@extend %regular-m;
			text-transform: uppercase;
			font-size: 14px;
		}
	}
}

.service{
	&__wrapper{
		@include row-flex();

		margin: 40px 0 120px;

		.block{
			@include col-padding();
			@include size(4);

			margin-top: 40px;
			padding-bottom: 40px;
			display: flex;
			&:nth-child(-n+3){
				border-bottom: 1px solid #e5e5e5;
			}
			&__img{
				width: 30%;
				text-align: center;

				img{
					width: 40px;
				}
			}
			&__info{
				width: 70%;
			}

			&__paragraph{
				@extend %regular-r;
				padding-top: 10px;
				font-size: 14px;
				line-height: 22px;
				color: $color_gray;
			}
		}
	}
}

.design{
	margin-bottom: 80px;
	background-image: url('../img/design-banner-bg.jpg');
	background-position: center center;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
	&__imgs{
		position: relative;
		text-align: center;
	}
	&__img-tablet{
		margin-bottom: -4px;
	}
	&__img-phone{
		position: absolute;
		bottom: 0;
		margin-bottom: -90px;
		left: 50%;
		margin-left: 100px;
	}
}
.offer{
	&__wrapper{
		@include row-flex();
	}
	&__img{
		@include col();
		@include size(6);
	}
	&__accordion{
		@include col();
		@include size(6);
	}
}

.accordion{
	margin-bottom: 5px;
	&__header{
		padding: 15px 0;
		display: flex;
		align-items: center;
		border: 1px solid #cccccc;

		img{
			padding: 0 15px;
		}

		i{
			margin-right: 0;
			margin-left: auto;
			padding: 0 15px;
			color: #cccccc;
			font-size: 24px;
		}
	}
	&__info{
		@extend %lightItalic-r;
		display: none;
		padding: 20px 25px 25px 20px;
		border: 1px solid #cccccc;
		border-top: 0;
		font-size: 15px;
		line-height: 24px;
		color: $color_gray;
		&_active{
			display: block;
		}
		p{
			padding-right: 20px;
			height: 135px;
			overflow: scroll;
		}
	}
}

.reviews{
	margin-top: 120px;
	background: #f8f8f8;
}
.reviews-slider{
	&__wrapper{
		position: relative;
		padding: 70px 0;
	}
	&__item{
		margin: 0 auto;
		width: 90%;
		display: flex;
	}
	&__icon{
	    min-width: 150px;
	    height: 150px;
	    display: flex;
	    justify-content: center;
	    align-items: center;
	    border: 2px solid #95e1d3;
	    color: #95e1d3;
	    font-size: 72px;
	}
	&__photo{
		position: relative;
	    min-width: 215px;
	    height: 215px;
	    border-radius: 50%;
		overflow: hidden;
		img{
		    height: 100%;
		    width: auto;
		    max-width: none;
		    position: absolute;
		    top: 50%;
		    left: 50%;
		    transform: translate(-50%,-50%);
		}
	}
	&__review{
		margin: 0 55px;
		&_photo{
			align-self: center;
		}
		p{
			@extend %lightItalic-r;
			padding-bottom: 30px;
			font-size: 24px;
			color: $color_gray;
		}
		span{
			@extend %regular-ks;
			font-size: 24px;
			color: $color_black;
			&:before{
				content: "";
				margin-right: 10px;
				display: inline-block;
				width: 60px;
				height: 3px;
				background: $color_pink;
			}
		}
	}
	&__btn{
		margin-top: -20px;
		position: absolute;
		top: 50%;
	    color: #cccccc;
		font-size: 20px;
		&_next{
			right: 0;
		}
	}
}
.our-team{
	margin-bottom: 100px;
	&__wrapper{
		@include row-flex();
	}
	.person{
		@include col();
		@include size(4);

		text-align: center;

		&__photo{
			background: $color_turquoise;
			cursor: pointer;

			img{
				vertical-align: middle;
				transition: all .5s;
				&:hover{
					transform: translate(-10px, -10px);
				}
			}
		}

		&__about{
			margin-top: 30px;
			font-size: 14px;
			.name{
				@extend %regular-m;
				text-transform: uppercase;
				color: $color_black;
			}
			.position{
				@extend %lightItalic-r;
				line-height: 38px;
				color: $color_gray;
			}
		}
	}
}

.logos{
	margin-top: 100px;
	padding: 70px 0;
	background: #f8f8f8;
	&__wrapper{
		@include row-flex();
	}
	&__item{
		@include col();
		@include size(2);

		display: flex;
    	justify-content: center;
    	align-items: center;
	}
}

.work{
	position: relative;
	&__offset{
		padding-top: 50%;
		height: 0;
	}
	&__content{
	    position: absolute;
	    top: 0;
	    left: 0;
	    width: 100%;
	    height: 100%;

		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		.item{
			flex: 1 1 50%;
			width: 25%;

		    background-size: cover;
		    background-position: center center;
		    background-repeat: no-repeat;
			&_1{
				background-image: url('../img/pic1.jpg');
			}
			&_2{
				background-image: url('../img/pic2.jpg');
			}
			&_3{
				background-image: url('../img/pic3.jpg');
			}
			&_4{
				background-image: url('../img/pic4.jpg');
			}
			&_5{
				flex: 1 1 100%;
				background-image: url('../img/pic5.jpg');
			}
			&_6{
				background-image: url('../img/pic6.jpg');
			}
			&_7{
				background-image: url('../img/pic7.jpg');
			}
		}
	}
}

.clients{
	background-image: url('../img/bg-clients.jpg');
	background-position: center center;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
	&__wrapper{
		@include row-flex();

		margin-top: 80px;
		padding-bottom: 100px;

		.client{
			@include col();
			@include size(6);

			margin-bottom: 30px;
			display: flex;

			&__photo{
				flex: 1 0 30%;
			    text-align: center;
			}
			&__opinion{
				flex: 1 1 55%;
				font-size: 14px;

				.name{
					@extend %regular-m;
					display: block;
					text-transform: uppercase;
					color: $color_black;
				}
				.pos{
					@extend %lightItalic-r;
					line-height: 38px;
					color: $color_black;
					&:after{
						content: "";
						margin: 10px 0 15px;
						display: block;
						width: 60px;
						height: 2px;
						background: $color_pink;
					}
				}
				.review{
					@extend %regular-r;
					color: #999999;
				}
			}
		}
	}
}

.blog{
	margin: 40px 0 90px;

	&__wrapper{
		@include row-flex();
	}
}

.story{
	@include col();
	@include size(4);

	display: flex;
	flex-direction: column;

	.story-banner{
		position: relative;
		&__date{
			@extend %bold-m;
			padding: 15px;
		    position: absolute;
		    bottom: 6%;
		    left: -3%;
			background: $color_turquoise;
			font-size: 30px;
			color: #fff;

			span{
				@extend %lightItalic-r;
				display: block;
				font-size: 15px;
			}
		}
	}

	h5{
	    padding: 20px 0;
	}

	&__info{
		@extend %regular-r;
		margin-bottom: 20px;
		font-size: 15px;
		line-height: 25px;
		color: $color_gray;
	}
	&__footer{
		margin: auto 0 0;
		padding: 20px 0;
    	border-top: 1px solid #ccc;
		color: $color_gray;
		i{
			@extend %lightItalic-r;

			&:before{
				color: $color_turquoise;
			}

			&:first-child{
				padding-right: 10px;
			}
		}
	}
}

.map{
	padding: 60px 0 50px;
	background: url("../img/bg-map.jpg");
	position: center center;
	background-size: cover;
	text-align: center;
	color: $color_pink;

	&__icon{
		font-size: 24px;
	}
	&__link{
		@extend %bold-m;
		margin: 20px 0;
		display: block;
		text-transform: uppercase;
		&:after{
			content: "";
			margin: 20px auto;
			display: block;
			width: 60px;
			height: 2px;
			background: $color_pink;
		}
	}
}

.footer{
	padding-top: 70px;
	background: #f8f8f8;

	&__wrapper{
		@include row-flex();

		padding-bottom: 70px;
		border-bottom: 1px solid #cccccc;
	}

	&__item{
		&_1{
			@include col();
			@include size(5);
		}
		&_2{
			@include col();
			@include size(4);
		}
		&_3{
			@include col();
			@include size(3);
		}
	}
	&__subhead{
		@extend %regular-m;
		margin-bottom: 40px;
		display: block;
		text-transform: uppercase;
		font-size: 14px;
		color: $color_black;
	}
	&__logo{
		@extend %bold-m;
		margin: 40px  0;
		color: $color_gray;
		font-size: 45px;
	}
	&__info{
		@extend %regular-r;
		margin: 40px 0 50px;
		font-size: 15px;
		color: $color_gray;
	}
	&__follow{
		@extend %regular-m;
		margin: 50px 0 20px;
		padding-bottom: 20px;
		border-bottom: 1px solid #ccc;
		font-size: 14px;
		color: $color_black;
		span{
			@extend %bold-m;
			font-size: 18px;
		}
	}
}
.social{
	margin: 20px 0 40px;
	display: flex;

	&__item{
		margin: 0 5px;
		i{
			color: $color_turquoise;
		}
	}

	&__head{
		@extend %lightItalic-r;
		font-size: 15px;
		color: $color_gray;
	}
}
.article{
	margin: 30px 0;
	display: flex;
	justify-content: space-between;

	&__img{
		vertical-align: middle;
	}

	&__cont{
	    align-self: center;
    	flex: 1 1 70%;
    	padding: 0 5%;
		align-self: center;
		p{
			@extend %regular-m;
			text-transform: uppercase;
			font-size: 12px;
		}
		span{
			@extend %lightItalic-r;
			font-size: 13px;
			color: $color_gray;
		}
	}
}

.insta-galery{
	margin: 40px 0 20px;
    display: flex;
    flex-wrap: wrap;

	&__item{
		margin: 0 2px 2px 0;
		flex: 1 1 30%;
	    align-self: center;

	    img{
	    	width: 100%;
	    	vertical-align: middle;
	    }
	}
}

.show-link{
	@extend %lightItalic-r;
	font-size: 13px;
	color: $color_gray;
}

.form{

	&__input{
		@extend %lightItalic-r;
		width: 40%;
		padding: 12px;
		border: 1px solid #cccccc;
		font-size: 15px;
		color: $color_black;
		&::-webkit-input-placeholder{color: #ccc;}
		&::-moz-placeholder{color: #ccc;}
		&:-moz-placeholder{color: #ccc;}
		&:-ms-input-placeholder{color: #ccc;}
	}
	&__btn{
		@extend %bold-m;
		margin-left: -5px;
	    padding: 12px 7%;
		text-transform: uppercase;
		font-size: 14px;
		border: 1px solid $color_turquoise;
		background: $color_turquoise;
		color: #fff;
		cursor: pointer;
	}
}
.copyright{
	@extend %regular-m;
    padding: 25px 0;
    text-align: center;
    font-size: 14px;
    color: $color_black;

    span{
    	color: $color_pink;
    }
}